.flashcard {
     width: 200px;
     height: 300px;
     border: 1px solid #ddd;
    border-radius: 8px;
    cursor: pointer;
    /* Other styles */
      
  }
  
  .flashcard-inner {
    width: 200px;
    height: 300px;
    position: relative;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }
  
  .flashcard-inner.flipped {
    transform: rotateY(180deg);
  }
  
  .flashcard-front,
  .flashcard-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
  }
  
  .flashcard-front {
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .flashcard-back {
    background: #f8f9fa;
    transform: rotateY(180deg);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  